@import url('https://fonts.googleapis.com/css?family=Work+Sans');
@import './variables.scss';

p, li{
  font-family: 'Work Sans', sans-serif;
}

h1, h2{
  font-family: 'Work Sans', sans-serif;
  font-variant: small-caps;
}

.App {
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.member-wrapper {
  border-radius: 25px;
  border: 2px solid $soft-black;
  padding: 20px;
  width: 300px;
  margin-bottom: 20px;
  margin-right: 10px;
  margin-left: 10px;
}

.member-picture {
  width: 150px;
  border-radius: 50%;
}

.icon {
  height: 30px;
}

.icon-link {
  text-decoration: none;
}

.contribution-list {
  list-style-type: circle;
  list-style-position: inside;

}

.contribution-item {
  text-align: left;
}



